@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
    font-family: 'Bebas Neue';
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    font-size: 20px;
    align-items: center;
    text-align: center;
    color: black;
    /* background-color: yellow; */
    padding: 0;
    padding-bottom: 40px;
}

.navbar a:hover{
    color: rgb(16, 27, 80);
}

.navbar a:visited{
    color: darkred;
}

.navbar a:link{
    color: black;
}


.title {
    /* background-color: red; */
    font-size: 50px;
    font-family: 'Bebas Neue';
    letter-spacing: 5px;
}

@media all and (max-width: 800px){
    body {
        font-size: 10px;
    }
}

p {
    color: black;
}

.main-text {
    /* padding-top: 40px; */
    margin: auto;
    width: 70%;
    border: 1px solid black;
    border-radius: 25px;
    box-shadow: 0 0 3px #333;
    background: rgb(255, 255, 255);
    transition: all 0.3s ease;
    font-size: 18px;
}

@media all and (max-width: 800px){
    .main-text{
        margin: auto;
    }
}

.projects-row{
    display: flex;
}

.speak-ez{
    flex: 33.33% 1;
    /* padding: 1px; */
}

.hogwarts{
    flex: 33.33% 1;
    /* padding: 1px; */
}

.goat{
    flex: 33.33% 1;
    /* padding: 1px; */
}

.speak-ez-img{
    width: 300px;
    border: 2px solid black;
    border-radius: 20%;
}

.goat-img{
    width: 300px;
    border: 2px solid black;
    border-radius: 20%;
}

.hogwarts-img{
    width: 300px;
    border: 2px solid black;
    border-radius: 20%;
}

.projects-p {
    margin: auto;
    width: 90%;
    border: 1px solid black;
    border-radius: 20px;
    box-shadow: 0 0 3px #333;
    background: rgb(255, 255, 255);
    transition: all 0.3s ease;  
    padding: 10px;
    font-size: 18px;
}

h2 {
    font-size: 23px;
    color: white;
    /* text-shadow: 10px 10px 30px black; */
}

.projects-row h2{
  font-family: 'Bebas Neue';
  letter-spacing: 4px;
  background: rgba(100, 10, 10, 0.7);
  width: 350px;
  height: 50px;
  border-radius: 30px;
  line-height: 50px;
  color: white;
  margin-bottom: 10px;
  margin-left: 48px;
}

@media all and (max-width: 800px){
    .projects-row{
        display: flex;
        flex-direction: column;
        font-size: 18px;
        /* margin-bottom: 40px; */
    }

    .hogwarts {
        margin: auto auto 10px auto;
    }

    .goat{
        margin: auto auto 30px auto;
    }


    .speak-ez{
        margin: auto auto 10px auto;
    }

    .projects-row h2{
        margin: auto auto 10px auto;
    }
}

@media all and (max-width: 1650px){
    .projects-row h2 {
     margin-left: auto;
     margin-right: auto;
    }
  }
body{
  background: url(https://images.pexels.com/photos/4827/nature-forest-trees-fog.jpeg?auto=compress&cs=tinysrgb&dpr=3&h=750&w=1260)no-repeat;
  background-size: cover;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

